/**
* @module ui-bb-paginator-ng
* @description
* Pagination directive
*
* @example
* // In an extension:
* // file: scripts/index.js
* import uiBbPaginatorNg from 'ui-bb-paginator-ng';
*
* export const dependencyKeys = [
*   uiBbPaginatorNg,
* ];
*
* // file: templates/template.ng.html
* <ui-bb-paginator-ng
*   class="bb-pagination"
*   data-first-text="First"
*   data-last-text="Last"
*   data-previous-text="Previous"
*   data-next-text="Next"
*   data-total-items="100"
*   data-items-per-page="10"
*   data-max-nav-pages="5"
*   data-change-page="changePage(params)">
* </ui-bb-paginator-ng>
*/

import angular from 'vendor-bb-angular';
import uiBbPaginationKey from 'vendor-bb-uib-pagination';

import uiBbPaginatorDirective from './directive';
import uiBbPaginatorController from './controller';

export default angular
  .module('ui-bb-paginator-ng', [uiBbPaginationKey])
  .directive('uiBbPaginatorNg', uiBbPaginatorDirective)
  .controller('uiBbPaginatorControllerNg', ['$scope', uiBbPaginatorController])
  .name;



